import axios from "axios";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { SET_PLANS } from "../../../../utilities/constants";
export default function EditPlans(props) {
  const [plan, setPlan] = useState({});
  const [price, setPrice] = useState({
    base_2price: "",
    base_15price: "",
    base_30price: "",
    customer2price: "",
    customer15price: "",
    customer30price: "",
  });
  const [inputPlan, setInputPlan] = useState({});
  const dispatch = useDispatch();
  const handleContinue = (e) => {
    dispatch({
      type: SET_PLANS,
      payload: inputPlan,
    });
    props.goToStep(5);
  };
  const handleBack = (e) => {
    props.goToStep(3);
  };
  const onBasePriceChange = (event) => {
    const { name, value } = event.target;
    const fieldName =
      name === "base_2price"
        ? "customer2price"
        : name === "base_15price"
        ? "customer15price"
        : name === "base_30price"
        ? "customer30price"
        : null;
    const customerprice =
      name === "base_2price"
        ? parseFloat(value || 0) + parseFloat(plan.twoPlan)
        : name === "base_15price"
        ? parseFloat(value || 0) + parseFloat(plan.fifteenPlan)
        : name === "base_30price"
        ? parseFloat(value || 0) + parseFloat(plan.thirtyPlan)
        : null;
    setPrice({ ...price, [fieldName]: customerprice });
  };
  const fetchProfits = async () => {
    let res = await axios.get("/api/plans");
    let plan = res.data[0];
    setPlan(plan);
  };
  useEffect(() => {
    fetchProfits();
  }, []);
  useEffect(() => {
    let component = true;
    if (component) {
      let { base_2price, base_15price, base_30price } = props.restaurant;
      let customer2pric = parseFloat(base_2price) + parseFloat(plan.twoPlan);
      let customer15pric =
        parseFloat(base_15price) + parseFloat(plan.fifteenPlan);
      let customer30pric =
        parseFloat(base_30price) + parseFloat(plan.thirtyPlan);
      setPrice({
        ...price,
        base_2price,
        base_15price,
        base_30price,
        customer2price: customer2pric,
        customer15price: customer15pric,
        customer30price: customer30pric,
      });
    }
    return () => {
      component = false;
    };
  }, [props.restaurant, plan]);
  return (
    <fieldset>
      <p className="mt-2">
        <strong>2 Days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price <strong className="text-danger">*</strong> &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_2price"
              defaultValue={price.base_2price}
              onChange={(e) => onBasePriceChange(e)}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer2price"
              defaultValue={price.customer2price}
            />
          </div>
        </div>
      </div>
      <p>
        <strong>15 days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price <strong className="text-danger">*</strong> &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_15price"
              defaultValue={price.base_15price}
              onChange={(e) => onBasePriceChange(e)}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer15price"
              defaultValue={price.customer15price}
            />
          </div>
        </div>
      </div>
      <p>
        <strong>30 Days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price <strong className="text-danger">*</strong> &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_30price"
              defaultValue={price.base_30price}
              onChange={(e) => onBasePriceChange(e)}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer30price"
              defaultValue={price.customer30price}
            />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2 "
            onClick={handleContinue}
          >
            Next
          </button>
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
